<template>
  <div id="app">
    <p class='tip' v-show='show'>
      Bojay来到地球上已经{{ time }}<i></i>
    </p>
    <audio :src='isHappy?happy:bg' id='music' ref='music' autoplay preload loop hidden></audio>
    <router-view/>
  </div>
</template>

<script>
import moment from 'moment'

require('moment-precise-range-plugin')

export default {
  name: 'App',
  data: () => ({
    show: false,
    start: moment('1998/06/15'),
    time: '',
    happy: require('@/assets/videos/0.mp3'),
    bg: require('@/assets/videos/1.mp3'),
    isHappy: true,
    audioStatus: 'pause'
  }),
  created() {
    setInterval(() => {
      let now = moment()
      let diff = moment.preciseDiff(this.start, now, true)
      this.time = ''
      if(diff.years) {
        this.time += diff.years + '年'
      }
      if(diff.months) {
        this.time += diff.months + '月'
      }
      if(diff.days) {
        this.time += diff.days + '天'
      }
      if(diff.hours) {
        this.time += diff.hours + '时'
      }
      if(diff.minutes) {
        this.time += diff.minutes + '分'
      }
      if(diff.seconds) {
        this.time += diff.seconds + '秒'
      }
    }, 1000)
  },
  mounted() {
    // document.addEventListener('touchstart', this.audioAutoPlay, false)
    // document.addEventListener('WeixinJSBridgeReady', this.audioAutoPlay, false)
    let oAudio = document.querySelector("#music")
    oAudio.onended = function() {
      oAudio.load()
      oAudio.play()
    }

    oAudio.addEventListener('playing', function() {
      this.audioStatus = 'playing'
    })

    oAudio.addEventListener('"pause"', function() {
      this.audioStatus = 'paused'
    })

    document.addEventListener('touchstart', function() {
      if(this.audioStatus === 'paused') {
        document.getElementById("bgmusic").play();
      }
    }, false)

    document.addEventListener("WeixinJSBridgeReady", function() {
      if(this.audioStatus === 'paused') {
        document.getElementById("bgmusic").play();
      }
    })
    this.audioAutoPlay()
  },
  methods: {
    audioAutoPlay() {
      let audio = document.getElementById('music')
      try {
        audio.play()
      } catch(e) {
        console.log(e)
      }
    }
  }
}
</script>

