<template>
  <div id="guide">
    <div id='happy'>Happy</div>
    <div class='flex' style='align-items: baseline'>
      <div id='age' class='font-lg' style='margin-right: 10px'>24th</div>
      <div id='tip'>birthday，Bojay!</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'j-guide',
  props: {},
  mounted() {
    let font = this.anime.timeline({
      easing: 'linear',
      delay: this.anime.stagger(50),
    })
    let tipsAnime = {
      translateX: 0,
      opacity: 1,
      duration: 500
    }
    font.add({
      targets: ['#happy', '#age', '#tip'],
      translateX: 100,
      opacity: 0,
      duration: 0
    }).add({
      targets: '#happy',
      ...tipsAnime
    }).add({
      targets: '#age',
      ...tipsAnime
    }).add({
      targets: '#tip',
      ...tipsAnime,
      complete: () => {
        setTimeout(() => {
          this.$router.push('/home')
        }, 1500)
      }
    })
    font.play()
    this.$parent.isHappy = false
  }
}
</script>

<style scoped>
#guide {
  height: 100vh;
  width: 100%;
  background: black;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  color: white;
  font-size: 1.5rem;
}
#happy, #age, #tip {
  opacity: 0;
}
</style>
