<template>
  <div>
    <div class='wrapper' ref='wrapper'>
      <div class='wrapper-content' ref='wrapperContent'>
        <div class='wrapper-item' v-for='(item, index) in data' :key='index' ref='wrapperItem'>
          <img :src='item.url' alt='' ref='wrapperItemImg'>
          <div v-if='label[index] >= 1998'>{{ label[index] }}</div>
        </div>
      </div>
    </div>
    <div class='back' @click='go'>
      back
    </div>
    <div v-if='content' class='info' @click='show=true'>
      info
    </div>
    <popup v-model="show" position="bottom" :style="{ maxHeight: '80%' }">
      <div class='content' v-html='content'></div>
      <div class='close' @click='show=false'>x</div>
    </popup>
  </div>
</template>

<script>
import BScroll from '@better-scroll/core'
import ScrollBar from '@better-scroll/scroll-bar'
import Popup from 'vant/lib/popup'
import 'vant/lib/popup/style'

BScroll.use(ScrollBar)

export default {
  name: 'j-photo',
  components: {
    Popup
  },
  props: {},
  data: () => ({
    data: null,
    label: null,
    content: '',
    show: false,
    offset: 0
  }),
  created() {
    this.data = this.config[this.$route.params.id].children
    this.label = this.config[this.$route.params.id].children_labels
    this.content = this.config[this.$route.params.id].content
    this.offset = this.$route.params.offset
  },
  mounted() {
    this.initScroll();
  },
  methods: {
    //横向滚动
    initScroll() {
      this.$nextTick(() => {
        const itemRefs = this.$refs.wrapperItem;
        if(!itemRefs.length) return;
        //计算总宽度
        this.width = itemRefs.reduce((prevItem, nextItem) => {
          const leftVal =
              typeof prevItem === 'number' ? prevItem : prevItem.offsetWidth;
          return leftVal + nextItem.offsetWidth;
        });
        if(itemRefs.length <= 1) {
          this.$refs.wrapperContent.style.width = this.width.clientWidth + 'px';
        } else this.$refs.wrapperContent.style.width = this.width + 'px';
        //初始化
        if(!this.scroll) {
          this.scroll = new BScroll(this.$refs.wrapper, {
            scrollX: true,
            probeType: 3,
            eventPassthrough: 'vertical'
          });
        } else {
          this.scroll.refresh();
        }
      });
    },
    go() {
      this.$router.push(`/home/${this.offset}`)
    }
  }
}
</script>

