  overflow: hidden;
  height: 100vh;
  background: linear-gradient(125deg, #2c3e50, #27ae60, #2980b9, #d9afd9, #97d9e1);
  background-size: 500%;
  animation: bgAnimation 5s linear infinite;
}

@keyframes bgAnimation {
  0%{
    background-position: 0% 50%;
  }
  50%{
    background-position: 100% 50%;
  }
  100%{
    background-position: 0% 50%;
  }
}

.wrapper-content {
  white-space: nowrap;
  height: 100vh;
  display: flex;
  align-items: center;
  position: relative;
  z-index: 1;
}

.wrapper-item {
  padding: 0 5rem;
  display: inline-flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.wrapper-item img {
  width: 25rem;
  max-height: 25rem;
}

.back {
  position: absolute;
  top: 1rem;
  left: 1rem;
  line-height: .8rem;
  font-size: .8rem;
  padding: .5rem 1rem;
  border: 1px solid;
  display: flex;
  z-index: 2;
}

.info {
  position: absolute;
  top: 1rem;
  right: 1rem;
  line-height: .8rem;
  font-size: .8rem;
  padding: .5rem 1rem;
  border: 1px solid;
  display: flex;
  z-index: 2;
}

.van-popup {
  border-top: 1px solid white;
  background-image: linear-gradient(to right, #434343 0%, #29323c 100%);
  background-size: 500%;
  animation: bgAnimation 5s linear infinite;
  color: white;
}

.content {
  padding: 1rem;
  line-height: 2rem;
}

.close {
  position: fixed;
  bottom: 1rem;
  right: 1rem;
  line-height: 2.5rem;
  font-size: 2rem;
  text-align: center;
  width: 2.5rem;
  background: black;
  border: 1px solid;
}
</style>
