<template>
  <div>
    <div class='outer'>
      <div id='container' class='container'>
        <div class='indicator-box' v-show='isRow'>
          <div class='indicator' :style='{left: left + "px"}'></div>
        </div>
        <div class='item' v-for='(item, index) in config' :key='index' ref='item'>
          <img :src='item.cover' alt='' ref='img' @click='go(index)'>
          <div class='msg-box' @click='go(index)' v-show='isRow' ref='msgBox'>
            <div>{{ item.title }}</div>
          </div>
        </div>
      </div>
    </div>
    <div class='btns'>
      <div v-if='content' class='info' @click='show=true'>
        info
      </div>
      <div class='switch' @click='switchLayout'>
        <svg v-if='!isRow' fill='none' xmlns='http://www.w3.org/2000/svg' width='26' height='20' viewBox='0 0 26 20'>
          <rect x='5' y='2' width='7' height='7' stroke-width='1' stroke='currentColor'
                vector-effect='non-scaling-stroke'
