                style='transform: translateX(-2px) translateY(5px) scaleX(1.57) scaleY(1.2); transform-origin: 8.5px 5.5px;'></rect>
          <rect x='14.5' y='2' width='7' height='7' stroke-width='1' stroke='currentColor' opacity='0'></rect>
          <rect x='5' y='11.45' width='7' height='7' stroke-width='1' stroke='currentColor' opacity='0'></rect>
          <rect x='14.5' y='11.45' width='7' height='7' stroke-width='1' stroke='currentColor'
                vector-effect='non-scaling-stroke' transform-origin='18px 14.950000286102295px'
                style='transform: translateX(2px) translateY(-5px) scaleX(1.4) scaleY(2); transform-origin: 18px 14.95px;'></rect>
        </svg>
        <svg v-else fill='none' xmlns='http://www.w3.org/2000/svg' width='26' height='20' viewBox='0 0 26 20'>
          <rect x='5' y='2' width='7' height='7' stroke-width='1' stroke='currentColor'
                vector-effect='non-scaling-stroke'
                transform-origin='8.5px 5.5px' style='transform: none; transform-origin: 8.5px 5.5px;'></rect>
          <rect x='14.5' y='2' width='7' height='7' stroke-width='1' stroke='currentColor' opacity='1'></rect>
          <rect x='5' y='11.45' width='7' height='7' stroke-width='1' stroke='currentColor' opacity='1'></rect>
          <rect x='14.5' y='11.45' width='7' height='7' stroke-width='1' stroke='currentColor'
                vector-effect='non-scaling-stroke' transform-origin='18px 14.950000286102295px'
                style='transform: none; transform-origin: 18px 14.95px;'></rect>
        </svg>
      </div>
    </div>
    <popup v-model='show' position='bottom' :style='{ maxHeight: "80%" }'>
      <div class='content' v-html='content'></div>
      <div class='close' @click='show=false'>x</div>
    </popup>
  </div>
</template>

<script>
import Popup from 'vant/lib/popup'
import 'vant/lib/popup/style'

export default {
  name: 'j-photo',
  components: {
    Popup
  },
  props: {},
  data: () => ({
    container: null,
    imgWidth: 300,
    isRow: false,
    show: false,
    content: '',
    first: false,
    scroll: 0,
    left: 0
  }),
  created() {
    this.$parent.show = true
    this.content = this.info
  },
  mounted() {
    this.container = document.querySelector('.container')
    this.init()
    document.querySelector('.outer').addEventListener('scroll', this.onScroll)

    /*document.querySelector('.indicator').addEventListener('mousedown', () => {
      this.mouseStatus = 'down'
      document.querySelector('.indicator').addEventListener('mousemove', (e) => {
        if(this.isRow && this.mouseStatus === 'down') {
          this.left = this.left + e.offsetX + 5
          let centerWidth = this.left / document.querySelector('.indicator-box').offsetWidth
          let center = centerWidth * parseFloat(this.container.style.width.replace('px', ''))
          let screenStart  = (center * 2 - document.body.clientWidth) / 2
          document.querySelector('.outer').scrollTo(parseFloat(screenStart), 0)
        }
      })
    })

    document.querySelector('.indicator').addEventListener('mouseup', () => {
      this.mouseStatus = 'up'
    })

    document.querySelector('.indicator').addEventListener('mouseout', () => {
      this.mouseStatus = 'up'
    })


    document.querySelector('.indicator').addEventListener('touchstart', () => {
      this.mouseStatus = 'down'
      document.querySelector('.indicator').addEventListener('touchmove', (e) => {
        if(this.isRow && this.mouseStatus === 'down') {
          this.left = this.left + e.offsetX + 5
          let centerWidth = this.left / document.querySelector('.indicator-box').offsetWidth
          let center = centerWidth * parseFloat(this.container.style.width.replace('px', ''))
          let screenStart  = (center * 2 - document.body.clientWidth) / 2
          document.querySelector('.outer').scrollTo(parseFloat(screenStart), 0)
        }
      })
    })

    document.querySelector('.indicator').addEventListener('touched', () => {
      this.mouseStatus = 'up'
    })*/
  },
  methods: {
    init() {
      if(this.$route.params.offset) {
        this.first = true
        this.setPosition({
          rows: 1,
          space: 40
        })
      } else {
        this.initItems()
        this.timer = setTimeout(() => {
          this.setPosition({
            rows: 1,
            space: 40
          })
        }, 2000)
      }
    },
    initItems() {
      this.$refs.img.forEach(e => {
        e.onload = () => {
          this.setPosition({
            rows: 6,
            space: 40
          })
        }
      })
    },
    setPosition(info) {
      let arr = new Array(info.rows)
      arr.fill(0)

      this.$refs.img.forEach((e, i) => {
        let minTop = Math.min(...arr)
        let index = arr.indexOf(minTop)
        // e.style.left = minTop + 'px'
        // this.$refs.msgBox[i].style.left = minTop + 'px'
        // this.$refs.msgBox[i].style.left = minTop + (e.width / 4) + 'px'
        // this.$refs.msgBox[i].style.width = e.width + 'px'
        this.$refs.item[i].style.left = minTop + 'px'
        this.$refs.item[i].style.display = 'flex'
        this.$refs.item[i].style.justifyContent = 'center'
        arr[index] = minTop + e.width + info.space
        if(info.rows === 1) {
          e.style.top = '50%';
          e.style.transform = 'translateY(-50%)'
        } else {
          e.style.top = index * this.imgWidth + (index + 1) * info.space + 'px'
        }
      })
      this.container.style.width = Math.max(...arr) + 'px'
      this.container.style.marginLeft = info.space + 'px'
      this.isRow = info.rows === 1
      if(this.isRow && this.$route.params.offset && this.first) {
        document.querySelector('.outer').scrollTo(parseFloat(this.$route.params.offset), 0)
        this.first = false
      }
    },
    switchLayout() {
      if(this.timer)
        clearTimeout(this.timer)
      let rows = 1
      if(this.isRow)
        rows = 6
      this.timer = setTimeout(this.setPosition({
        rows,
        space: 40
      }), 50)
    },
    go(e) {
      this.$router.push(`/photo/${e}/${this.scroll}`)
    },
    onScroll(e) {
      this.scroll = e.target.scrollLeft
      let screenStart = this.scroll
      let screenEnd = screenStart + document.body.clientWidth
      let center = (screenStart + screenEnd) / 2
      let width = parseFloat(this.container.style.width.replace('px', ''))
      this.left = (center / width) * (document.querySelector('.indicator-box').offsetWidth)
    }
  }
}
</script>

