import Vue from 'vue'

import VueRouter from 'vue-router'
import Index from '@/views/index'
import Guide from '@/views/guide'
import Home from '@/views/home'
import Photo from '@/views/photo'


Vue.use(VueRouter);

const routes = [
    {
        path: '/',
        name: 'Index',
        component: Index
    },
    {
        path: '/guide',
        name: 'Guide',
        component: Guide
    },
    {
        path: '/home/:offset?',
        name: 'Home',
        component: Home
    },
    {
        path: '/photo/:id/:offset?',
        name: 'Photo',
        component: Photo
    }
]

const router = new VueRouter({
    routes,
    mode:'history'

});

export default router
